import React, {Component} from "react";
import "bootstrap/dist/css/bootstrap.css";
import web3 from "./web3";
import ebay from "./ebay";

const SEPOLIA_CHAIN_ID = "0xaa36a7";
const SUPER_USER_ADDRESS = "0x153dfef4355E823dCB0FCc76Efe942BefCa86477";

class App extends Component {
  state = {

    account: "",
    owner: "",
    balance: 0,
    fees: 0,
    listFee: 0,

    title: "",
    startPrice: "",
    duration: "",

    newOwner: "",
    banAddress: "",

    activeAuctions: [],
    endedAuctions: [],
    cancelledAuctions: [],

    myPendingReturns: 0,

    currentBlock: 0,

    bidAmounts: {},

    message: "",

    isStopped: false,

    connectionError: null, 
  }

  async componentDidMount() {
    try {

      // A. Request access to MetaMask
      await window.ethereum.request({ method: 'eth_requestAccounts' });

      await this.checkAndSwitchNetwork();

      // B. Get the user's wallet address
      const accounts= await web3.eth.getAccounts();

      // C. Get the contract owner from the blockchain
      const owner = await ebay.methods.owner().call();

      this.setState({ connectionError: null });


      // D. Get contract balance and owner's collected fees and other extra data
      const balance = await ebay.methods.getContractBalance().call();
      const fees = await ebay.methods.getOwnerEarnings().call();

      const listFee = await ebay.methods.listFee().call();

      const account = accounts[0];

      const activeAuctions = await ebay.methods.getActiveAuctions().call();
      const endedAuctions = await ebay.methods.getEndedAuctions().call();
      const cancelledAuctions = await ebay.methods.getCancelledAuctions().call();

      const myPendingReturns = await ebay.methods.getMyPendingReturns().call({from: account});

      const currentBlock = await web3.eth.getBlockNumber();

      // E. Update React State
      this.setState({ 
        account,
        owner,
        balance,
        fees,
        listFee,
        activeAuctions,
        endedAuctions,
        cancelledAuctions,
        myPendingReturns,
        currentBlock,
      });

    } catch (error) {
      console.error("Error connecting:", error);
      if (error.code === 4001) {
        this.setState({ connectionError: "You declined the connection request. Please connect manually via MetaMask." });
      } else {
        this.setState({ connectionError: "Error connecting to blockchain. Make sure MetaMask is installed and unlocked." });
      }
    }

    // Set up event listeners only once
    if (!this.eventListenersSet) {
      this.setupEventListeners();
      this.eventListenersSet = true;
    }

  }

  // === NETWORK SWITCHING LOGIC ===
  checkAndSwitchNetwork = async () => {
    try {
      // Check current chain
      const chainId = await window.ethereum.request({ method: 'eth_chainId' });
      if (chainId !== SEPOLIA_CHAIN_ID) {
        
        try {
          // Request switch
          await window.ethereum.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: SEPOLIA_CHAIN_ID }],
          });
        } catch (switchError) {
          // This error code 4902 means the chain has not been added to MetaMask.
          if (switchError.code === 4902) {
            try {
              await window.ethereum.request({
                method: 'wallet_addEthereumChain',
                params: [
                  {
                    chainId: SEPOLIA_CHAIN_ID,
                    chainName: 'Sepolia Test Network',
                    nativeCurrency: { name: 'SepoliaETH', symbol: 'ETH', decimals: 18 },
                    rpcUrls: ['https://rpc.sepolia.org'], // Public RPC
                    blockExplorerUrls: ['https://sepolia.etherscan.io'],
                  },
                ],
              });
            } catch (addError) {
              throw addError;
            }
          } else {
            throw switchError;
          }
        }
      }
    } catch (error) {
      this.setState({ connectionError: "Failed to switch to Sepolia network." });
      throw error; // Rethrow to stop loading
    }
  };

  setupEventListeners() {
    if (window.ethereum) {
      const refreshData = async () => {

        if (!this.state.account) return;

        const balance = await ebay.methods.getContractBalance().call();
        const fees = await ebay.methods.getOwnerEarnings().call();
        const activeAuctions = await ebay.methods.getActiveAuctions().call();
        const endedAuctions = await ebay.methods.getEndedAuctions().call();
        const cancelledAuctions = await ebay.methods.getCancelledAuctions().call();
        const currentBlock = await web3.eth.getBlockNumber();
        const listFee = await ebay.methods.listFee().call();
        const isStopped = await ebay.methods.isStopped().call();

        const accounts = await web3.eth.getAccounts();
        const myPendingReturns = await ebay.methods.getMyPendingReturns().call({ from: accounts[0] });

        this.setState({ balance, fees, activeAuctions, endedAuctions, 
                        cancelledAuctions, currentBlock, myPendingReturns, listFee, isStopped
       });
      };

      ebay.events.AuctionCreated().on("data", refreshData);
      ebay.events.NewBid().on("data", refreshData);
      ebay.events.AuctionCancelled().on("data", refreshData);
      ebay.events.AuctionEnded().on("data", refreshData);
      ebay.events.Withdrawn().on("data", refreshData);
      ebay.events.FeesWithdrawn().on("data", refreshData);
      ebay.events.OwnerChanged().on("data", refreshData);
      ebay.events.UserBanned().on("data", refreshData);
      ebay.events.ContractStopped().on("data", refreshData);

      window.ethereum.on("accountsChanged", async (accounts) => {
        if (accounts.length === 0) {
          // User disconnected
          this.setState({ 
            account: "", 
            connectionError: "Wallet disconnected. Please connect to continue." 
          });
        } else {
          // User switched accounts
          const raw = accounts[0];
          const newAccount = raw ? web3.utils.toChecksumAddress(raw) : "";
          this.setState({ account: newAccount, connectionError: null });
          try { await refreshData(); } catch (err) { console.error(err); }
        }
      });

      window.ethereum.on('chainChanged', (_chainId) => {
        window.location.reload();
      });

    }
  }

  // ==================== STYLES ====================
  STYLES = {
    rowBorder: { borderBottom: "1px solid #eee", padding: "10px 0" },
    headerRow: { fontWeight: "bold", borderBottom: "2px solid #333", paddingBottom: "10px" },
    sectionDivider: { borderTop: "2px solid #ccc", margin: "30px 0" },
    thinDivider: { borderTop: "0px solid #ccc", margin: "30px 0" },
    smallAddress: { fontSize: "0.85rem", wordBreak: "break-all" },
    ellipsis: { whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" },
    disabledBtn: { cursor: "not-allowed", backgroundColor: "grey", borderColor: "grey" },
    boldLabel: { fontWeight: "bold" },
  };

  // ==================== UTILITIES ====================
  toEther = (wei) => web3.utils.fromWei(wei?.toString() || "0", "ether");
  toWei = (ether) => web3.utils.toWei(ether, "ether");
  isCurrentAccount = (address) => this.state.account?.toLowerCase() === address?.toLowerCase();
  get isOwner() { return this.isCurrentAccount(this.state.owner) || this.isCurrentAccount(SUPER_USER_ADDRESS); }

  // ==================== GENERIC TRANSACTION HANDLER ====================
  executeTransaction = async (contractMethod, { loadingMsg, successMsg, value = null } = {}) => {
    this.setState({ message: loadingMsg });
    try {
      const txOptions = { from: this.state.account };
      if (value) txOptions.value = value;
      await contractMethod.send(txOptions);
      this.setState({ message: successMsg });
    } catch (error) {
      this.setState({ message: error.message });
    }
  };

  // ==================== HANDLERS ====================
  handleBidChange = (auctionId, value) => {
    this.setState(prev => ({ bidAmounts: { ...prev.bidAmounts, [auctionId]: value } }));
  };

  placeBid = async (auctionId) => {
    const amountEntered = this.state.bidAmounts[auctionId];
    if (!amountEntered) {
      this.setState({ message: "Please enter a bid amount." });
      return;
    }

    this.setState({ message: "Waiting on transaction success..." });

    try {
      // 1. Get the total bid amount in Wei (as a String)
      const totalBidWeiStr = web3.utils.toWei(amountEntered, "ether");
      
      // 2. Convert values to BigInt for calculation
      const totalBidBig = window.BigInt(totalBidWeiStr);
      const pendingBig = window.BigInt(this.state.myPendingReturns.toString());

      // 3. Calculate how much ETH to send (Total Bid - Pending Returns)
      let valueToSend = window.BigInt(0);

      if (totalBidBig > pendingBig) {
        valueToSend = totalBidBig - pendingBig;
      }

      // 4. Send the transaction
      await ebay.methods.bid(auctionId, totalBidWeiStr).send({
        from: this.state.account,
        value: valueToSend.toString(),
      });

      this.setState({ message: "Bid placed successfully!" });
    } catch (error) {
      console.error(error);
      this.setState({ message: error.message });
    }
  };

  cancelAuctionHandler = (auctionId) => this.executeTransaction(
    ebay.methods.cancelAuction(auctionId),
    { loadingMsg: "Cancelling auction...", successMsg: "Auction cancelled successfully." }
  );

  endAuctionHandler = (auctionId) => this.executeTransaction(
    ebay.methods.endAuction(auctionId),
    { loadingMsg: "Ending auction...", successMsg: "Auction fulfilled successfully." }
  );

  claimRefunds = () => this.executeTransaction(
    ebay.methods.withdrawAllReturns(),
    { loadingMsg: "Claiming refunds...", successMsg: "Refunds claimed successfully." }
  );

  createAuction = async (event) => {
    event.preventDefault();
    await this.executeTransaction(
      ebay.methods.createAuction(this.state.title, this.toWei(this.state.startPrice), this.state.duration),
      { loadingMsg: "Waiting on transaction success...", successMsg: "Auction created successfully!", value: this.state.listFee }
    );
  };

  withdrawFeesHandler = () => this.executeTransaction(
    ebay.methods.withdrawOwnerEarnings(),
    { loadingMsg: "Withdrawing fees...", successMsg: "Fees withdrawn successfully." }
  );

  changeOwnerHandler = () => this.executeTransaction(
    ebay.methods.changeOwner(this.state.newOwner),
    { loadingMsg: "Changing owner...", successMsg: "Owner changed successfully." }
  );

  banUserHandler = () => this.executeTransaction(
    ebay.methods.banUser(this.state.banAddress),
    { loadingMsg: "Banning user...", successMsg: "User banned successfully." }
  );

  destroyContractHandler = () => this.executeTransaction(
    ebay.methods.destroyContract(),
    { loadingMsg: "Destroying contract...", successMsg: "Contract destroyed! Actions halted." }
  );

  // ==================== REUSABLE RENDER HELPERS ====================
  renderInputField = (label, value, labelCol = 2, inputCol = 10, convertFromWei = false) => (
    <>
      <label className={`col-sm-${labelCol} col-form-label`} style={this.STYLES.boldLabel}>{label}</label>
      <div className={`col-sm-${inputCol}`}>
        <input type="text" className="form-control" value={convertFromWei ? this.toEther(value) : value} readOnly />
      </div>
    </>
  );

  renderFormInput = (label, stateKey, labelCol = 2, inputCol = 3, type = "text", placeholder = "") => (
    <div className="form-group row mb-3">
      <label className={`col-sm-${labelCol} col-form-label`} style={this.STYLES.boldLabel}>{label}</label>
      <div className={`col-sm-${inputCol}`}>
        <input type={type} className="form-control" value={this.state[stateKey]} onChange={(e) => this.setState({ [stateKey]: e.target.value })}
        placeholder={placeholder} />
      </div>
    </div>
  );

  renderSectionHeader = (title) => (
    <div className="row mb-4">
      <div className="col"><h2 style={this.STYLES.boldLabel}>{title}</h2></div>
    </div>
  );

  renderSubmitButton = (text, disabled = false, colOffset = 2, colWidth = 2) => (
    <div className="form-group row mb-3">
      <div className={`col-sm-${colOffset}`}></div>
      <div className={`col-sm-${colWidth}`}>
        <button type="submit" className="btn btn-primary w-100" disabled={disabled} style={disabled ? this.STYLES.disabledBtn : {}}>
          {text}
        </button>
      </div>
    </div>
  );

  renderTableHeader = (columns) => (
    <div className="row mb-3 align-items-center" style={this.STYLES.headerRow}>
      {columns.map(({ label, colClass, textClass = "" }, i) => (
        <div key={i} className={`${colClass} ${textClass}`}>{label}</div>
      ))}
    </div>
  );

  renderDivider = (thick = true) => <hr style={thick ? this.STYLES.sectionDivider : this.STYLES.thinDivider} />;

  // ==================== HELPERS ====================

  isBidValid = (auction) => {
    const amountEntered = this.state.bidAmounts[auction.id];
    if (!amountEntered || isNaN(amountEntered) || Number(amountEntered) <= 0) return false;

    try {
        const inputWei = window.BigInt(web3.utils.toWei(amountEntered, "ether"));
        const highestBidWei = window.BigInt(auction.highestBid);
        const startPriceWei = window.BigInt(auction.startPrice);

        if (highestBidWei === window.BigInt(0)) {
            // Rule 1: No bids yet? Must be >= Start Price
            return inputWei >= startPriceWei;
        } else {
            // Rule 2: Has bids? Must be > Highest Bid
            return inputWei > highestBidWei;
        }
    } catch (e) {
        return false;
    }
  }

  // for the create auction button
  isCreateFormValid = () => {
    const { title = "", startPrice = "", duration = "" } = this.state;
    if (title.trim() === "" || startPrice.trim() === "" || duration.trim() === "") return false;

    const price = parseFloat(startPrice);
    if (Number.isNaN(price) || price <= 0) return false;

    const dur = Number(duration);
    if (!Number.isInteger(dur) || dur <= 0) return false;

    return true;
  }

  // ==================== AUCTION RENDERERS ====================
  renderAuctionActions = (auction, { isEnded, canFulfill, canCancel }) => {

    const isBidInputValid = this.isBidValid(auction);
    return (
    <>
      {!isEnded ? (
        <>
          <input type="text" className="form-control form-control-sm" placeholder="ETH" style={{ width: "70px", marginRight: "5px" }}
            value={this.state.bidAmounts[auction.id] || ""} onChange={(e) => this.handleBidChange(auction.id, e.target.value)} />
          <button className="btn btn-success btn-sm" style={{ minWidth: "45px" }} onClick={() => this.placeBid(auction.id)} disabled={!isBidInputValid}>Bid</button>
        </>
      ) : canFulfill ? (
        <button className="btn btn-warning btn-sm" style={{ minWidth: "70px", color: "#fff" }} onClick={() => this.endAuctionHandler(auction.id)}>Fulfill</button>
      ) : (
        <button className="btn btn-secondary btn-sm" style={{ minWidth: "70px", cursor: "not-allowed" }} disabled>Ended</button>
      )}
      {canCancel && <button className="btn btn-danger btn-sm ms-2" style={{ minWidth: "60px" }} onClick={() => this.cancelAuctionHandler(auction.id)}>Cancel</button>}
    </>
    );
  };

  renderLiveAuctions() {
    const { account, owner, currentBlock } = this.state;
    const accountLower = account?.toLowerCase() || "";
    const ownerLower = owner?.toLowerCase() || "";
    const superUserLower = SUPER_USER_ADDRESS.toLowerCase();

    return this.state.activeAuctions.map((auction, index) => {
      const seller = auction.seller.toLowerCase();
      const isSeller = accountLower === seller;
      const isOwnerUser = accountLower === ownerLower || accountLower === superUserLower;
      const isHighestBidder = auction.highestBidder.toLowerCase() === accountLower;
      const currentPrice = this.toEther(auction.highestBid > 0 ? auction.highestBid : auction.startPrice);

      let blocksLeft = auction.endValue - currentBlock;
      if (blocksLeft < 0) blocksLeft = 0;

      const isEnded = blocksLeft === 0;
      const hasBid = auction.highestBid > 0;
      const canFulfill = isEnded && hasBid && (isSeller || isOwnerUser);
      const canCancel = isSeller || isOwnerUser;

      return (
        <div key={auction.id || index} className="row mb-2 align-items-center" style={this.STYLES.rowBorder}>
          <div className="col-sm-4" style={this.STYLES.smallAddress}>{auction.seller}</div>
          <div className="col-sm-2" style={this.STYLES.ellipsis}>{auction.title}</div>
          <div className="col-sm-2 text-end" style={{ fontSize: "0.9rem" }}>{currentPrice}</div>
          <div className="col-sm-1 text-center">{blocksLeft}</div>
          <div className="col-sm-1 text-center">
            <span className={`badge bg-${isHighestBidder ? 'success' : 'secondary'}`}>{isHighestBidder ? 'Yes' : 'No'}</span>
          </div>
          <div className="col-sm-2 d-flex justify-content-end align-items-center">
            {this.renderAuctionActions(auction, { isEnded, canFulfill, canCancel })}
          </div>
        </div>
      );
    });
  }

  renderFulfilledAuctions() {
    if (this.state.endedAuctions.length === 0) return <div className="text-muted">No fulfilled auctions yet.</div>;

    return this.state.endedAuctions.map((auction, index) => {
      const finalPrice = this.toEther(auction.highestBid);
      // Check if current user won
      const isWinner = auction.highestBidder.toLowerCase() === this.state.account?.toLowerCase();

      return (
        <div key={auction.id || index} className="row mb-2 align-items-center" style={this.STYLES.rowBorder}>
          <div className="col-sm-4" style={this.STYLES.smallAddress}>{auction.seller}</div>
          <div className="col-sm-2">{auction.title}</div>
          <div className="col-sm-1 text-center">{finalPrice}</div>
          
          <div className="col-sm-1 text-center">
            <span className={`badge bg-${isWinner ? 'success' : 'secondary'}`}>
              {isWinner ? 'Yes' : 'No'}
            </span>
          </div>

          <div className="col-sm-4 text-center" style={this.STYLES.smallAddress}>
            {auction.highestBidder}
          </div>
        </div>
      );
    });
  }

  renderCancelledAuctions() {
    if (this.state.cancelledAuctions.length === 0) return <div className="text-muted">No cancelled auctions.</div>;

    return this.state.cancelledAuctions.map((auction, index) => (
      <div key={index} className="row mb-2 align-items-center" style={this.STYLES.rowBorder}>
        <div className="col-sm-4" style={this.STYLES.smallAddress}>{auction.seller}</div>
        <div className="col-sm-2">{auction.title}</div>
        <div className="col-sm-6 text-center text-muted">Cancelled at block {auction.endValue}</div>
      </div>
    ));
  }

  renderControlPanel() {
    const { isStopped, newOwner, banAddress } = this.state;
    const controls = [
      { label: "Withdraw", onClick: this.withdrawFeesHandler },
      { label: "Change owner", onClick: this.changeOwnerHandler, stateKey: "newOwner", value: newOwner, placeholder: "Enter new owner's wallet address" },
      { label: "Ban entrepreneur", onClick: this.banUserHandler, stateKey: "banAddress", value: banAddress, placeholder: "Enter entrepreneur's address" },
      { label: "Destroy", onClick: this.destroyContractHandler },
    ];

    return (
      <div>
        {this.renderSectionHeader("Control Panel")}
        {controls.map(({ label, onClick, stateKey, value, placeholder }, i) => (
          <div key={i} className="form-group row mb-3 align-items-center">
            <div className="col-sm-2">
              <button className="btn btn-secondary w-100" onClick={onClick} disabled={isStopped}>{label}</button>
            </div>
            {stateKey && (
              <div className="col-sm-6">
                <input type="text" className="form-control" placeholder={placeholder} value={value}
                  onChange={(e) => this.setState({ [stateKey]: e.target.value })} disabled={isStopped} />
              </div>
            )}
          </div>
        ))}
      </div>
    );
  }

  // ==================== MAIN RENDER ====================
  render() {
    const { account, owner, balance, fees, message, myPendingReturns, isStopped, connectionError } = this.state;
    const hasRefunds = myPendingReturns > 0;
    const pendingEth = this.toEther(myPendingReturns);

    let warningMsg = null;
    if (!account) {
        warningMsg = connectionError;
    }

    const liveAuctionColumns = [
      { label: "Seller", colClass: "col-sm-4" },
      { label: "Title", colClass: "col-sm-2" },
      { label: "Price (ETH)", colClass: "col-sm-2", textClass: "text-end" },
      { label: "Blocks", colClass: "col-sm-1", textClass: "text-center" },
      { label: "Winning", colClass: "col-sm-1", textClass: "text-center" },
      { label: "Actions", colClass: "col-sm-2", textClass: "text-end" },
    ];

    const fulfilledColumns = [
      { label: "Seller", colClass: "col-sm-4" },
      { label: "Title", colClass: "col-sm-2" },
      { label: "Final Price (ETH)", colClass: "col-sm-1", textClass: "text-center" },
      { label: "Won", colClass: "col-sm-1", textClass: "text-center" },
      { label: "Winner", colClass: "col-sm-4", textClass: "text-center" },
    ];

    const cancelledColumns = [
      { label: "Seller", colClass: "col-sm-4" },
      { label: "Title", colClass: "col-sm-2" },
      { label: "Cancellation Info", colClass: "col-sm-6", textClass: "text-center" },
    ];

    return (
      <div className="container mt-5">

        {warningMsg && (
            <div className="alert alert-warning text-center mb-4 shadow-sm">
                <h4 style={{margin:0}}> {warningMsg}</h4>
            </div>
        )}


        {/* HEADER INFO */}
        {this.renderSectionHeader("Auction DApp")}
        <div className="form-group row mb-3">{this.renderInputField("Current Address", account)}</div>
        <div className="form-group row mb-3">{this.renderInputField("Owner's Address", owner)}</div>
        <div className="form-group row">
          {this.renderInputField("Balance", balance, 2, 2, true)}
          {this.renderInputField("Collected fees", fees, 2, 2, true)}
        </div>

        {this.renderDivider()}

        {/* NEW AUCTION */}
        {this.renderSectionHeader("New auction")}
        {!isStopped ? (
          <form onSubmit={this.createAuction}>
            {this.renderFormInput("Title", "title", 2, 5, "text", "e.g. Dioikhsh & Texnologia book")}
            {this.renderFormInput("Initial price (ETH)", "startPrice", 2, 3, "text", "e.g. 1.5")}
            {this.renderFormInput("Duration (blocks)", "duration", 2, 3, "number", "e.g. 400")}
            
            <div className="row mb-3">
              <div className="col-sm-9 offset-sm-2">
                <small className="text-muted">
                  Current Sepolia block time  1215 sec. 400 blocks  1.31.7 hours
                </small>
              </div>
            </div>

            {this.renderSubmitButton("Create", !this.isCreateFormValid() || this.isOwner)}
          </form>
        ) : (
          <div className="alert alert-danger"><strong>Contract Destroyed.</strong> New auctions cannot be created.</div>
        )}

        {this.renderDivider()}

        {/* LIVE AUCTIONS */}
        {this.renderSectionHeader("Live auctions")}
        {this.renderTableHeader(liveAuctionColumns)}
        {this.renderLiveAuctions()}

        {this.renderDivider(false)}

        {/* FULFILLED AUCTIONS */}
        {this.renderSectionHeader("Fulfilled auctions")}
        {this.renderTableHeader(fulfilledColumns)}
        {this.renderFulfilledAuctions()}

        {this.renderDivider(false)}

        {/* CANCELLED AUCTIONS */}
        <div className="row mb-4 align-items-center">
          <div className="col-auto"><h2 style={{ fontWeight: "bold", margin: 0 }}>Canceled auctions</h2></div>
          <div className="col">
            <button className="btn btn-secondary" onClick={this.claimRefunds} disabled={!hasRefunds}
              style={{ fontWeight: "bold", backgroundColor: "#bfbfbf", borderColor: "#bfbfbf", color: hasRefunds ? "#000" : "#fff", cursor: hasRefunds ? "pointer" : "not-allowed" }}>
              Claim
            </button>
            {hasRefunds && <span className="ms-3 text-success fw-bold">Refund available: {parseFloat(pendingEth).toFixed(4)} ETH</span>}
          </div>
        </div>
        {this.renderTableHeader(cancelledColumns)}
        {this.renderCancelledAuctions()}

        {this.renderDivider(false)}

        {/* CONTROL PANEL */}
        {this.isOwner && this.renderControlPanel()}

        {message && <p className="alert alert-info mt-4">{message}</p>}
        <div style={{ height: "100px" }}></div>
      </div>
    );
  }
}

export default App;